<template>
  <div id="app" @click="createParticles">
    <NavBar v-if="!isWelcomePage" />
    <router-view></router-view> <!--  -->
    <BottomBar v-if="!isWelcomePage" />
    <div class="particles" id="particles-js"></div> <!--  -->
  </div>
</template>

<script>
import BottomBar from '@/components/BottomBar.vue';
import NavBar from '@/components/NavBar.vue';
import { useUserStore } from '@/stores/userStore';
import { usePetStore } from './stores/petStore';
import { ref, onValue, update } from 'firebase/database'; //  Firebase  API
import { database } from '@/firebase'; //  Firebase 
import gsap from 'gsap'; //  gsap
export default {
  name: 'App',
  components: {
    BottomBar,
    NavBar,
  },
  computed: {
    isWelcomePage() {
      return this.$route && this.$route.name === 'Welcome';
    }
  },
  mounted() {
    this.initializeUserData();
    this.setupPassiveIncome(); // 
  },
  methods: {
    initializeUserData() {
      const userStore = useUserStore();
      const storedUser = localStorage.getItem('user');

      if (storedUser) {
        const user = JSON.parse(storedUser);
        userStore.setUser(user);
        this.setupFirebaseSync(user.uid);

      }
    },
    setupFirebaseSync(userId) {
      const userStore = useUserStore();

      const userRef = ref(database, `users/${userId}`);
      const petStore = usePetStore();
      //  Firebase 
      onValue(userRef, (snapshot) => {
        const data = snapshot.val();
        if (data) {
          userStore.setVirtualCoins(data.virtualCoins);
          userStore.setDiamonds(data.diamonds || 0); // 
          userStore.setPetLevel(data.petLevel || 1);

          petStore.selectPet(data.selectedPetIndex.selectedPetIndex);
          console.log('selectedPetIndex:', data.selectedPetIndex.selectedPetIndex);
          console.log('Data updated from Firebase:', data.virtualCoins, data.diamonds);
        }
      });

      //  Firebase 
      setInterval(() => {
        onValue(userRef, (snapshot) => {
          const data = snapshot.val();
          if (data) {
            userStore.setVirtualCoins(data.virtualCoins);
            userStore.setDiamonds(data.diamonds || 0); // 
            userStore.setPetLevel(data.petLevel || 1);
            console.log('Data updated from Firebase:', data.virtualCoins, data.diamonds);
          }
        });

      }, 3000); // 10
    },
    setupPassiveIncome() {
      const userStore = useUserStore();
      const userId = userStore.user ? userStore.user.uid : null;

      if (userId) {
        const userRef = ref(database, `users/${userId}`);

        //  10 
        setInterval(() => {
          const newCoins = userStore.virtualCoins + 1;
          // const newDiamonds = userStore.diamonds + 1; // 
          userStore.setVirtualCoins(newCoins);
          // userStore.setDiamonds(newDiamonds); // 

          //  Firebase
          update(userRef, {
            virtualCoins: newCoins,
          });
        }, 3000); // 1011
      }
    },
    // 
    createParticles(event) {
      console.log('createParticles');
      const particlesCount = 30; // 
      const particlesContainer = document.getElementById('particles-js'); // 
      if (!particlesContainer) {
        console.error('Particles container not found');
        return; // 
      }

      const x = event.clientX; //  X 
      const y = event.clientY; //  Y 

      for (let i = 0; i < particlesCount; i++) {
        const particle = document.createElement('div');
        particle.className = 'particle'; // 
        particle.style.left = `${x}px`; // 
        particle.style.top = `${y}px`;
        particlesContainer.appendChild(particle); // 

        const randomX = Math.random() * 200 - 100; //  X 
        const randomY = Math.random() * 200 - 100; //  Y 

        //  GSAP 
        gsap.to(particle, {
          x: randomX,
          y: randomY,
          opacity: 0, // 
          duration: 0.5,
          onComplete: () => particle.remove() // 
        });
      }
    }


  }
};


</script>

<style>
body {
  margin: 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}

#app {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.particles {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
  z-index: 9999;
}

.particle {
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #ff4500;
  /*  */
  pointer-events: none;
}
</style>
