        :description="''"
        :image="'./pet/pet1.png'"
        @close="isPopupVisible = false"
        />
    </div>
</template>
  
  <script>
  import AchievementPopup from '../components/AchievementPopup.vue';
  
  export default {
    components: {
      AchievementPopup,
    },
    data() {
      return {
        isPopupVisible: false,
      };
    },
    methods: {
      showPopup() {
        this.isPopupVisible = true;
      },
    },
  };
  </script>
  