<template>
    <nav class="nav-bar">
        <div class="nav-item coins">
            <font-awesome-icon :icon="['fas', 'coins']" class="icon" />{{ virtualCoins }}
            <div ref="addCoinAnime" class="add-coin-anime">+ {{ addCoinAmount }}</div>
        </div>
        <div class="nav-item diamonds">
            <font-awesome-icon :icon="['fas', 'gem']" class="icon" /> {{ diamonds }}
            <div ref="addDimondAnime" class="add-dimond-anime">+ {{ addDimondAmount }}</div>
        </div> <!--  -->
        <div class="nav-item username">
            Hi, {{ user ? user.displayName : 'Guest' }}
        </div>
        <!-- <button class="logout-btn" @click="handleLogout" v-if="user">Logout</button> -->
        <button class="nav-item2" @click="navigateTo('Profile')">
            <font-awesome-icon :icon="['fas', 'user']" class="icon" />
        </button>
    </nav>
</template>

<script>
import { useUserStore } from "@/stores/userStore";
import { computed, ref, watch, onMounted, nextTick } from 'vue'; //  computed
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome"; //  FontAwesomeIcon
import { useRouter } from 'vue-router'; //  vue-router
import gsap from 'gsap'; //  gsap
export default {
    components: {
        FontAwesomeIcon, //  FontAwesomeIcon 
    },

    setup() {
        const userStore = useUserStore();
        const addCoinAnime = ref(null);
        const addCoinAmount = ref(0); // 
        const addDimondAnime = ref(null);
        const addDimondAmount = ref(0); // 

        //  computed 
        const user = computed(() => userStore.user);
        const virtualCoins = computed(() => userStore.virtualCoins);
        const diamonds = computed(() => userStore.diamonds); // 
        const router = useRouter();

        const navigateTo = (path) => {
            router.push({ name: path });
        };

        const handleLogout = () => {
            userStore.logout();
        };

        // 

        const triggerCoinAnimation = () => {
            if (!addCoinAnime.value) return;
            addCoinAnime.value.style.display = 'block';
            gsap.fromTo(
                addCoinAnime.value,
                { opacity: 0, y: -20 },
                { opacity: 1, y: 0, duration: 0.5, ease: "power1.out" }
            );

            // 
            gsap.to(addCoinAnime.value, {
                opacity: 0,
                delay: 1,
                duration: 0.5,
                ease: "power1.in",
                onComplete: () => {
                    // 
                    if (addCoinAnime.value) {
                        addCoinAnime.value.style.display = 'none';
                    }

                }
            });
        };

        // 
        const triggerDimondAnimation = () => {
            if (!addDimondAnime.value) return;
            addDimondAnime.value.style.display = 'block';
            gsap.fromTo(
                addDimondAnime.value,
                { opacity: 0, y: -20 },
                { opacity: 1, y: 0, duration: 0.5, ease: "power1.out" }
            );

            // 
            gsap.to(addDimondAnime.value, {
                opacity: 0,
                delay: 1,
                duration: 0.5,
                ease: "power1.in",
                onComplete: () => {
                    // 
                    if (addDimondAnime.value) {
                        addDimondAnime.value.style.display = 'none';
                    }

                }
            });
        };

        //  virtualCoins 
        watch(virtualCoins, (newVal, oldVal) => {
            if (newVal > oldVal) {
                console.log('newVal', newVal);
                addCoinAmount.value = newVal - oldVal;
                triggerCoinAnimation();
            }
        });
        watch(diamonds, (newVal, oldVal) => {
            if (newVal > oldVal) {
                console.log('newVal', newVal);
                addDimondAmount.value = newVal - oldVal;
                triggerDimondAnimation();
            }
        });

        // 
        onMounted(async () => {
            await nextTick();
            if (addCoinAnime.value) {
                addCoinAnime.value.style.display = 'none';
            }
            if (addDimondAnime.value) {
                addDimondAnime.value.style.display = 'none';
            }
        });

        return {
            user,
            virtualCoins,
            diamonds, // 
            handleLogout,
            addCoinAnime,
            addCoinAmount,
            addDimondAnime,
            addDimondAmount,
            navigateTo
        };


    },
    // methods: {
    //     navigateTo(path) {
    //         this.$router.push(path);
    //     }
    // }

};
</script>

<style scoped>
.nav-item2 {
    display: flex;
    align-items: center;
    color: white;
    border: none;
    background: none;
    cursor: pointer;
    transition: color 0.3s ease;
}

.nav-item2:hover {
    color: #3498db;
}

.nav-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2c3e50;
    color: white;
    padding: 25px 25px;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    box-sizing: border-box;
}

.nav-item {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    display: flex;
    align-items: center;
    /*  icon  */
}

.coins {
    flex: 1;
    text-align: left;
}

.diamonds {
    flex: 1;
    text-align: left;
}

.add-coin-anime {

    color: yellow;
    font-weight: bold;
    font-size: 14px;
    padding-left: 5%;
    pointer-events: none;
    opacity: 0;
    /*  */
}

.add-dimond-anime {

    color: rgb(45, 148, 228);
    font-weight: bold;
    font-size: 14px;
    padding-left: 5%;
    pointer-events: none;
    opacity: 0;
    /*  */
}


.username {
    flex: 1;
    text-align: center;
}

.logout-btn {
    flex: 1;
    text-align: right;
    background-color: #c0392b;
    color: white;
    font-size: 14px;
    padding: 8px 12px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    max-width: 80px;
}

.icon {
    margin-right: 8px;
    /*  icon  */
    font-size: 20px;
    /*  icon  */
}
</style>