import { createRouter, createWebHistory } from "vue-router";
import WelcomePage from "@/pages/WelcomePage.vue";
import MainPage from "@/pages/MainPage.vue";
import PetPage from "@/pages/PetPage.vue";
import ShopPage from "@/pages/ShopPage.vue";
import TaskPage from "@/pages/TaskPage.vue";
import ProfilePage from "@/pages/ProfilePage.vue";
import New1TaskPage from "@/pages/New1TaskPage.vue";
import Testing1Page from "@/pages/Testing1Page.vue";
import TestPopup from "@/pages/TestPopup.vue";
import SocialPage from "@/pages/SocialPage.vue";

import New2TaskPage from "@/pages/New2TaskPage.vue";


const routes = [
  {
    path: "/",
    name: "Welcome",
    component: WelcomePage,
  },
  {
    path: "/main",
    name: "Main",
    component: MainPage,
    meta: { requiresAuth: true }, // 
  },
  {
    path: "/pet",
    name: "Pet",
    component: PetPage,
    meta: { requiresAuth: true }, // 
  },
  {
    path: "/shop",
    name: "Shop",
    component: ShopPage,
    meta: { requiresAuth: true }, // 
  },
  {
    path: "/task",
    name: "Task",
    component: TaskPage,
    meta: { requiresAuth: true }, // 
  },
  {
    path: "/newtask",
    name: "NewTask",
    component: New1TaskPage,
    meta: { requiresAuth: true }, // 
  },
  {
    path: "/profile",
    name: "Profile",
    component: ProfilePage,
    meta: { requiresAuth: true }, // 
  },
  {
    path: "/test",
    name: "test",
    component: Testing1Page,
    meta: { requiresAuth: true }, // 
  },
  {
    path: "/test2",
    name: "test2",
    component: New2TaskPage,
    meta: { requiresAuth: true }, // 
  },
  {
    path: "/testpopup",
    name: "testpopup",
    component: TestPopup,
    meta: { requiresAuth: true },
  },
  {
    path: "/social",
    name: "Social",
    component: SocialPage,
    meta: { requiresAuth: true }, // 
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

// 
router.beforeEach((to, from, next) => {
  const user = JSON.parse(localStorage.getItem("user")); // 
  if (to.matched.some((record) => record.meta.requiresAuth) && !user) {
    next("/");
  } else {
    next();
  }
});

export default router;
